import { FC } from "react";
import * as classes from "./FinishPage.module.scss";
import Page from "@/shared/ui/Page/Page";
import { AButton } from "@/shared/ui/Button/AAButton";
import { useAppDispatch } from "@/app/lib/hooks/redux";
import { EventItemResultType } from "@/widgets/EventItem/model/EventItemSlice";
import { getResultMenu } from "@/widgets/EventItem";
import { initial } from "@/app";
import { bxRedirect } from "@/processes/event/model/EventThunk";


const FinishPage: FC = () => {

    const dispatch = useAppDispatch()

    const redirect = (to: 'deal' | 'company' | 'tasks') => { dispatch(bxRedirect(to)) }
    return (
        <Page color="grey">
            <div className={classes.wrapper}>

                <div className={`${classes.buttons} col-md-6 mt-2`}>
                    <AButton
                        title="к сделкам"
                        align="center"
                        color="blue"
                        size="big"
                        clickHendler={() => redirect('deal')}
                        isActive={true}
                    />
                    <AButton
                        title="к компаниям"
                        align="center"
                        color="blue"
                        size="big"
                        clickHendler={() => redirect('company')}
                        isActive={true}
                    />
                        <AButton
                        title="к задачам"
                        align="center"
                        color="blue"
                        size="big"
                        clickHendler={() => redirect('tasks')}
                        isActive={true}
                    />
                    {/* <AButton
                        title="+ создать"
                        align="center"
                        color="blue"
                        size="big"
                        clickHendler={() => setResultMenuStatus(EventItemResultType.NEW)}
                        isActive={true}
                    /> */}
                </div>



            </div>
        </Page>

    );
};
export default FinishPage